.tabs__block .tabs__control {
  width: 100%;
  margin-bottom: 15px; }
  .tabs__block .tabs__control ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex; }
    .tabs__block .tabs__control ul li {
      flex-grow: 1; }
      .tabs__block .tabs__control ul li a {
        display: block;
        text-align: center;
        font-weight: 500;
        font-size: 12px;
        line-height: 14px;
        border-bottom: 6px solid transparent;
        padding: 13px 15px;
        padding-top: 19px;
        text-transform: uppercase;
        color: #4C4C4C;
        text-decoration: none;
        transition: all .25s ease; }
        .tabs__block .tabs__control ul li a:hover {
          background-color: #FF4778;
          color: #ffffff; }
        .tabs__block .tabs__control ul li a.active {
          border-bottom-color: #BEA4E1; }
          .tabs__block .tabs__control ul li a.active:hover {
            background-color: transparent;
            color: #4C4C4C; }

.tabs__block .tabs__content h3 {
  margin-top: 35px;
  margin-bottom: 25px;
  font-weight: 600;
  font-size: 18px;
  line-height: 128.91%;
  color: #000; }
  @media screen and (max-width: 767px) {
    .tabs__block .tabs__content h3 {
      font-size: 16px; } }

.tabs__block .tabs__content p {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  margin-bottom: 15px; }

.tabs__block .tabs__content-item {
  display: none; }
  .tabs__block .tabs__content-item.active {
    display: block; }
